import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatIconModule } from '@angular/material/icon';
import { Route, RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { VersionControlResolver } from 'src/app/resolvers/versionControl.resolver';
import { SharedModule } from 'src/app/shared/shared.module';
import { WidgetsModule } from 'src/app/widgets/chq-widgets.module';
import { BookingTypeComponent } from './booking-type/booking-type.component';
import { JobsTasksComponent } from './jobs-tasks/jobs-tasks.component';
import { OperationsLayoutComponent } from './operations-layout/operations-layout.component';
import { RepairTypeComponent } from './repair-type/repair-type.component';
import { SchedulingComponent } from './scheduling/scheduling.component';
import { FormsModule } from '@angular/forms';

const casesRoutes: Route[] = [
  {
    path: '', 
    component: BookingTypeComponent,
    resolve: { version: VersionControlResolver }
  },
  { 
    path: 'repair-type', 
    component: RepairTypeComponent, 
    resolve: { version: VersionControlResolver }
  },
  { 
    path: 'jobs-tasks', 
    component: JobsTasksComponent, 
    resolve: { version: VersionControlResolver }
  },
  { 
    path: 'scheduling', 
    component: SchedulingComponent, 
    resolve: { version: VersionControlResolver }
  },
];

@NgModule({
  declarations: [
    OperationsLayoutComponent,
    BookingTypeComponent,
    RepairTypeComponent,
    JobsTasksComponent,
    SchedulingComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(casesRoutes),
    WidgetsModule,
    SharedModule,
    MatDatepickerModule,
    MatIconModule,
    TranslateModule,
    FormsModule
  ],
  exports: [ RouterModule, OperationsLayoutComponent,
    MatDatepickerModule ]
})
export class OperationsModule { }
